@import '../_fonts'

.section.pults
  position: relative
  min-height: 100vh
  width: 100%

  background-color: #242d3a


  .container
    display: table
    height: 100vh

    .blocks
      display: table-cell
      vertical-align: middle

      .block
        color: white
        text-align: left

      .block.main
        width: 700px

        font-family: $f_helvetica
        font-weight: 300

        .main-1
          opacity: 0.9

          text-transform: uppercase
          font-size: 35.1px
          line-height: 50.71px

        .main-2
          color: #b9d3f5
          opacity: 0.8

          font-size: 16px
          line-height: 26px
          margin-top: 20px

      .block.parts
        margin-left: 5%
        margin-top: 5%

        font-family: $f_helvetica
        font-size: 16px
        line-height: 30px

        td
          padding: 1% 2%

          .caption
            color: #52b7f7
            font-weight: 600

          p
            opacity: 0.6
            color: #b9d3f5
            font-weight: 300


  .block.cleaner
    position: absolute
    right: 17%
    bottom: 50%
    transform: translateY(50%) translateX(50%)

